
@charset "UTF-8";
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
  /*display: flex;*/
  height: 100%;
}
/*html,body{height:100%}*/
html, body, div, ul, li, h1, h2, h3, h4, h5, h6, p, dl, dt, dd, ol, form, input, textarea, th, td, select {
  margin: 0;
  padding: 0;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html, body {
  height: 100%;
}
body {
  font-family: "Microsoft YaHei";
  font-size: 14px;
  color: #333;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}
ul, ol {
  list-style: none;
}
img {
  border: none;
  vertical-align: middle;
}
a {
  text-decoration: none;
  color: #232323;
}
table {
  border-collapse: collapse;
  table-layout: fixed;
}
input, textarea {
  outline: none;
  border: none;
}
textarea {
  resize: none;
  overflow: auto;
}
button {
  border: 0;
  background-color: transparent;
}
.clearfix {
  zoom: 1;
}
.clearfix:after {
  content: ".";
  width: 0;
  height: 0;
  visibility: hidden;
  display: block;
  clear: both;
  overflow: hidden;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.tl {
  text-align: left;
}
.tc {
  text-align: center;
}
.tr {
  text-align: right;
}
.ellipse {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.inline {
  display: inline-block;
  *display: inline;
  *zoom: 1;
}
.page {
  display: -webkit-box;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  height: 100%;
}
/*flex内容区*/
.page .box {
  position: relative;
  /* autoprefixer: off */
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  /* autoprefixer: on */
  flex: 1;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  /*height: 100%;*/
  background-color: #fff;
}
/*flex头部*/
.header {
  display: -webkit-box;
}
.header * {
  display: block;
}
/*flex底部，按钮平均分布*/
.footer {
  overflow: hidden;
  /*background: #fff;*/
}
/*  .foot-nav {
    display: -webkit-box;
    height: 2rem;
    line-height: 2rem;
  }

  .foot-nav * {
    display: block;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
    flex: 1;
  }*/
.xlist {
  margin-top: 11px;
  margin-bottom: 10px;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  /*border-right: 1px solid #ccc;*/
}
.xlist .xitem {
    height: 43px;
    line-height: 43px;
    border-bottom: 1px solid #ccc;
    margin-left: 20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.xlist .xitem div {
      padding: 0 10px;
}
.xlist .xitem div img {
        width: 30px;
        height: 30px;
}
.xlist .xitem div span {
        font-size: 13px;
}
.xlist .xitem .center {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.xlist .xitem .qrcode div img {
      width: 80px;
      height: 80px;
}
.xlist .qrCode {
    height: 90px;
    line-height: 90px;
    border-bottom: 1px solid #ccc;
    margin-left: 20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.xlist .qrCode div {
      padding: 0 10px;
}
.xlist .qrCode div img {
        width: 30px;
        height: 30px;
}
.xlist .qrCode div span {
        font-size: 13px;
}
.xlist .qrCode .center {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
}
.xlist .qrCode .qrcode div img {
      width: 80px;
      height: 80px;
}
.xlist :last-child {
    border-bottom: 0;
}
/***********路由动画***************************************/
/* 可以设置不同的进入和离开动画 */
/* 设置持续时间和动画函数 */
.slide-fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.slide-fade-leave-active {
  -webkit-transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}
.bounce-enter-active {
  -webkit-animation: bounce-in .5s;
          animation: bounce-in .5s;
}
.bounce-leave-active {
  animation: bounce-in .5s reverse;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
  /*  30% {
      transform: scale(0.4);
    }*/
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
  /*  30% {
      transform: scale(0.4);
    }*/
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}



